<template>
  <div v-if="isClient">
    <BModal id="ProductCardModal" :hide-header="true" :hide-footer="true" v-slot="{ visible }" :key="quantity">
      <!-- @ok="clickOk" @cancel="clickCancel" -->
      <BRow no-gutters>
        <BCol md="4" cols="12">
          <img :src="product.image.large" v-if="product.image.large" />
        </BCol>
        <BCol md="8" cols="12" class="position-relative">
          <div class="product-name-wrapper">
            <p class="my-4 product-name">{{ product.name }}</p>
          </div>
          <p class="my-price">
            &euro;{{ priceRange.maximum_price.final_price.value.toFixed(2).replace(".", ",") }}
          </p>
          <span class="continue-shopping" @click="hide">
            < {{ $t("continue_shopping") }} </span>
              <BRow no-gutters class="btn-text-wrapper w-100 ">
                <BCol md="7">
                  <span class="added-product mb-10 mb-md-0">
                    {{ $t("added_product") }}
                  </span>
                </BCol>
                <BCol md="5" class="text-end">
                  <BButton type="button" variant="secondary" @click="clickOk" class="ml-20 complete-order">
                    {{ $t("complete_order") }}
                  </BButton>
                </BCol>
              </BRow>
        </BCol>
      </BRow>
      <!-- <section class="more-products product-section" v-if="products && products.length > 0">
            <div class="container">
              <span class="section-title d-block font-weight-normal text-center">Klanten bestelden ook</span>
              <MoreProductsCarousel class="pt-5 in-modal" :products="products" type="modal" v-if="visible" />
            </div>
          </section> -->
    </BModal>
  </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import { useRouter } from "vue-router";
import { useModal } from "bootstrap-vue-next";
const { hide } = useModal("ProductCardModal");
// import MoreProductsCarousel from "../MoreProductsCarousel.vue";

const isClient = ref(false);

defineProps({
  product: Object,
  priceRange: Object,
  quantity: Number,
  products: Array,
});

const router = useRouter();
const clickOk = () => {
  router.push("/checkout");
};
const clickCancel = () => {
  window.scrollTo(0, 0);
};

onMounted(() => {
  isClient.value = true;
});
</script>

<style>
.in-modal {
  .available-sizes-list {
    flex-grow: 1;
    height: auto !important;
  }
}
</style>
