<template>
  <div class="d-flex justify-content-between pt-10" v-if="isLoggedIn">
    <div v-if="priceRange.minimum_price.final_price.value != priceRange.maximum_price.final_price.value"
      class="top-wrap tw1">
      <span class="normal-price d-block" v-if="product?.manufacturer_price?.price?.value > 0">
        {{ $t("advice_price") }}:
        <span>{{ formatCurrency(product?.manufacturer_price?.price?.value) }}</span>
      </span>
      <div
        v-if="product?.manufacturer_price?.price.value == null && priceRange.maximum_price.regular_price.value > priceRange.minimum_price.final_price.value"
        class="d-flex align-items-end price-selection">
        <span class="normal-price">{{ $t("advice_price") }}:
          <span>{{ formatCurrency(priceRange.maximum_price.regular_price.value) }}</span>
        </span>
      </div>
      <div class="d-flex align-items-end price-selection">
        <span v-html="getProductPriceRange.text"></span>
        <span class="discount-comment" v-if="getProductPriceRange.discountType == 1">{{ $t("discount") }}
          {{ priceRange.minimum_price.discount.percent_off.toFixed(0) }}%</span>
        <span class="discount-comment2" v-if="getProductPriceRange.discountType == 2">
          {{ $t("to_discount") }}
          {{ getMaxDiscount }}%
        </span>
      </div>
    </div>
    <div v-if="priceRange.minimum_price.final_price.value == priceRange.maximum_price.final_price.value"
      class="top-wrap tw2">
      <span class="normal-price d-block" v-if="product?.manufacturer_price?.price.value > 0">{{ $t("advice_price") }}:
        <span>{{ formatCurrency(product?.manufacturer_price?.price.value) }}</span>
      </span>
      <div v-if="priceRange.minimum_price.regular_price.value > priceRange.minimum_price.final_price.value"
        class="d-flex align-items-end price-selection mb-10">
        <span class="normal-price">{{ $t("advice_price") }}:
          <span>{{ formatCurrency(priceRange.minimum_price.regular_price.value) }}</span></span>
      </div>
      <div class="d-flex align-items-end price-selection" v-html="getProductPriceRange.text"></div>
      <div class="d-flex align-items-end price-selection" v-if="priceRange.minimum_price.discount.percent_off > 0">
        <span class="discount-comment">
          {{ $t("discount") }}
          {{ priceRange.minimum_price.discount.percent_off.toFixed(0) }}%</span>
      </div>
    </div>
    <div v-if="product.brand && product.brand.logo" class="product-logo">
      <b-link :href="`/` + product.brand.urlKey">
        <img :src="product.brand.logo" :alt="product.brand.title" />
      </b-link>
    </div>
  </div>
  <span v-else class="login-see-price">{{ $t("login_see_price") }}</span>
</template>

<script setup>
import { computed } from "vue";
import { useStore } from "vuex";

const props = defineProps({
  product: Object,
  priceRange: Object,
  productConfig: Object,
  productBundle: Array,
});

const store = useStore();
const isLoggedIn = computed(() => store.getters["user/getIsLoggedIn"]);

const formatCurrency = (amount) => {
  const formatter = new Intl.NumberFormat("nl-NL", {
    style: "currency",
    currency: "EUR",
  });
  return formatter.format(amount).replace("Â ", "");
};

const finalPrice = computed(() => {
  if (props.product.__typename == "ConfigurableProduct") {
    if (store.getters["product/getCurrentChildSku"] != null) {
      const found = props.productConfig.variants.find((v) => {
        if (v.product.sku == store.getters["product/getCurrentChildSku"]) {
          return true;
        }
      });
      if (found != null) {
        return found.product.price_range.minimum_price.final_price.value.toFixed(2);
      }
    }
    if (
      props.product.price_range.minimum_price.final_price.value !=
      props.product.price_range.maximum.final_price.value
    ) {
      return (
        props.product.price_range.minimum_price.final_price.value.toFixed(2) +
        " - " +
        props.product.price_range.maximum_price.final_price.value.toFixed(2).replace(".", ",")
      );
    }
    return props.product.price_range.minimum_price.final_price.value.toFixed(2);
  }
  if (
    props.product.price_range.minimum_price.final_price.value !=
    props.product.price_range.maximum_price.final_price.value
  ) {
    return (
      props.product.price_range.minimum_price.final_price.value.toFixed(2).replace(".", ",") +
      " - " +
      props.product.price_range.maximum_price.final_price.value.toFixed(2).replace(".", ",")
    );
  }
  return props.product.price_range.minimum_price.final_price.value.toFixed(2);
});

const getProductPriceRange = computed(() => {
  if (props.product.__typename == "SimpleProduct") {
    return {
      text: '<span class="currentPrice">' + formatCurrency(finalPrice.value) + "</span>",
      discountType: 1,
    };
  } else if (props.product.__typename == "BundleProduct") {
    if (props.productBundle) {
      let lowestPrice = props.product.price_range.minimum_price.final_price.value;
      let highestPrice = props.product.price_range.maximum_price.final_price.value;
      if (lowestPrice == highestPrice) {
        return {
          text: '<span class="currentPrice">' + formatCurrency(lowestPrice) + "</span>",
          discountType: 1,
        };
      } else {
        return {
          text:
            '<span class="currentPrice">' +
            formatCurrency(lowestPrice) +
            " - " +
            formatCurrency(highestPrice) +
            "</span>",
          discountType: 2,
        };
      }
    } else {
      return 1;
    }
  } else {
    if (props.productConfig) {
      let lowestPrice = props.product.price_range.maximum_price.regular_price.value;
      let highestPrice = props.product.price_range.minimum_price.final_price.value;
      props.productConfig.variants.forEach((v) => {
        if (v.product.price_range.minimum_price.final_price.value < lowestPrice) {
          lowestPrice = v.product.price_range.minimum_price.final_price.value;
        }
        if (v.product.price_range.maximum_price.final_price.value > highestPrice) {
          highestPrice = v.product.price_range.maximum_price.final_price.value;
        }
      });
      if (lowestPrice == highestPrice) {
        return {
          text: '<span class="currentPrice">' + formatCurrency(lowestPrice) + "</span>",
          discountType: 1,
        };
      } else {
        return {
          text:
            '<span class="currentPrice">' +
            formatCurrency(lowestPrice) +
            " - " +
            formatCurrency(highestPrice) +
            "</span>",
          discountType: 2,
        };
      }
    } else {
      return 1;
    }
  }
});

const getMaxDiscount = computed(() => {
  if (props.priceRange.minimum_price && props.priceRange.maximum_price) {
    return Math.max(
      props.priceRange.minimum_price.discount.percent_off,
      props.priceRange.maximum_price.discount.percent_off
    ).toFixed(0);
  } else if (props.priceRange.minimum_price) {
    return props.priceRange.minimum_price.discount.percent_off.toFixed(0);
  } else {
    return props.priceRange.maximum_price.discount.percent_off.toFixed(0);
  }
});
</script>
